"use client"

import { useState } from "react"
import { Dialog, DialogContent, DialogHeader, DialogTitle } from "@/components/ui/dialog"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { Label } from "@/components/ui/label"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { createTransaction, createMercadoPagoPayment } from "@/lib/api"
import { toast } from "sonner"

interface TransactionDialogProps {
  open: boolean
  onOpenChange: (open: boolean) => void
  type: 'income' | 'expense'
}

type TransactionStatus = 'pending' | 'completed' | 'cancelled'
type PaymentMethod = 'cash' | 'credit_card' | 'debit_card' | 'pix' | 'transfer' | 'mercado_pago'

interface FormData {
  category: string
  description: string
  amount: number
  date: string
  customerId: string
  productId: string
  serviceId: string
  paymentMethod: PaymentMethod
  status: TransactionStatus
  notes: string
}

export function TransactionDialog({ open, onOpenChange, type }: TransactionDialogProps) {
  const [loading, setLoading] = useState(false)
  const [paymentLoading, setPaymentLoading] = useState(false)
  const [createdTransactionId, setCreatedTransactionId] = useState<string | null>(null)
  const [formData, setFormData] = useState<FormData>({
    category: "",
    description: "",
    amount: 0,
    date: new Date().toISOString().split('T')[0],
    customerId: "",
    productId: "",
    serviceId: "",
    paymentMethod: "cash",
    status: "pending",
    notes: ""
  })

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setLoading(true)
    try {
      const response = await createTransaction({
        ...formData,
        type,
        amount: Number(formData.amount)
      })
      
      setCreatedTransactionId(response.data.id)
      
      toast.success(
        type === 'income' 
          ? "Entrada registrada com sucesso!"
          : "Saída registrada com sucesso!"
      )

      if (formData.paymentMethod !== 'mercado_pago') {
        onOpenChange(false)
      }
    } catch (error) {
      console.error("Error creating transaction:", error)
      toast.error(
        type === 'income'
          ? "Erro ao registrar entrada. Por favor, tente novamente."
          : "Erro ao registrar saída. Por favor, tente novamente."
      )
    } finally {
      setLoading(false)
    }
  }

  const handleMercadoPagoPayment = async () => {
    if (!createdTransactionId) return

    setPaymentLoading(true)
    try {
      const response = await createMercadoPagoPayment(createdTransactionId)
      const { initPoint } = response.data

      // Open Mercado Pago checkout in a new window
      window.open(initPoint, '_blank')
      onOpenChange(false)
    } catch (error) {
      console.error("Error creating Mercado Pago payment:", error)
      toast.error("Erro ao criar pagamento no Mercado Pago. Por favor, tente novamente.")
    } finally {
      setPaymentLoading(false)
    }
  }

  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent className="sm:max-w-[500px] bg-black/90 border-purple-500/20">
        <DialogHeader>
          <DialogTitle className="text-white">
            {type === 'income' ? 'Nova Entrada' : 'Nova Saída'}
          </DialogTitle>
        </DialogHeader>
        <form onSubmit={handleSubmit} className="space-y-4">
          <div className="grid grid-cols-2 gap-4">
            <div className="space-y-2">
              <Label htmlFor="category" className="text-white">Categoria</Label>
              <Select
                value={formData.category}
                onValueChange={(value) => setFormData({ ...formData, category: value })}
              >
                <SelectTrigger id="category" className="bg-white/10 border-purple-500/20 text-white">
                  <SelectValue />
                </SelectTrigger>
                <SelectContent className="bg-black/90 border-purple-500/20">
                  {type === 'income' ? (
                    <>
                      <SelectItem value="service" className="text-white hover:bg-purple-500/10">Serviço</SelectItem>
                      <SelectItem value="product_sale" className="text-white hover:bg-purple-500/10">Venda de Produto</SelectItem>
                      <SelectItem value="other" className="text-white hover:bg-purple-500/10">Outros</SelectItem>
                    </>
                  ) : (
                    <>
                      <SelectItem value="supplies" className="text-white hover:bg-purple-500/10">Suprimentos</SelectItem>
                      <SelectItem value="tools" className="text-white hover:bg-purple-500/10">Ferramentas</SelectItem>
                      <SelectItem value="utilities" className="text-white hover:bg-purple-500/10">Utilidades</SelectItem>
                      <SelectItem value="rent" className="text-white hover:bg-purple-500/10">Aluguel</SelectItem>
                      <SelectItem value="salary" className="text-white hover:bg-purple-500/10">Salário</SelectItem>
                      <SelectItem value="other" className="text-white hover:bg-purple-500/10">Outros</SelectItem>
                    </>
                  )}
                </SelectContent>
              </Select>
            </div>
            <div className="space-y-2">
              <Label htmlFor="amount" className="text-white">Valor</Label>
              <Input
                id="amount"
                type="number"
                min="0"
                step="0.01"
                value={formData.amount}
                onChange={(e) => setFormData({ ...formData, amount: parseFloat(e.target.value) })}
                className="bg-white/10 border-purple-500/20 text-white"
                required
              />
            </div>
            <div className="space-y-2">
              <Label htmlFor="date" className="text-white">Data</Label>
              <Input
                id="date"
                type="date"
                value={formData.date}
                onChange={(e) => setFormData({ ...formData, date: e.target.value })}
                className="bg-white/10 border-purple-500/20 text-white"
                required
              />
            </div>
            <div className="space-y-2">
              <Label htmlFor="paymentMethod" className="text-white">Forma de Pagamento</Label>
              <Select
                value={formData.paymentMethod}
                onValueChange={(value: PaymentMethod) => setFormData({ ...formData, paymentMethod: value })}
              >
                <SelectTrigger id="paymentMethod" className="bg-white/10 border-purple-500/20 text-white">
                  <SelectValue />
                </SelectTrigger>
                <SelectContent className="bg-black/90 border-purple-500/20">
                  <SelectItem value="cash" className="text-white hover:bg-purple-500/10">Dinheiro</SelectItem>
                  <SelectItem value="credit_card" className="text-white hover:bg-purple-500/10">Cartão de Crédito</SelectItem>
                  <SelectItem value="debit_card" className="text-white hover:bg-purple-500/10">Cartão de Débito</SelectItem>
                  <SelectItem value="pix" className="text-white hover:bg-purple-500/10">PIX</SelectItem>
                  <SelectItem value="transfer" className="text-white hover:bg-purple-500/10">Transferência</SelectItem>
                  <SelectItem value="mercado_pago" className="text-white hover:bg-purple-500/10">Mercado Pago</SelectItem>
                </SelectContent>
              </Select>
            </div>
            {type === 'income' && (
              <div className="space-y-2">
                <Label htmlFor="customerId" className="text-white">Cliente</Label>
                <Input
                  id="customerId"
                  value={formData.customerId}
                  onChange={(e) => setFormData({ ...formData, customerId: e.target.value })}
                  className="bg-white/10 border-purple-500/20 text-white"
                />
              </div>
            )}
          </div>
          <div className="space-y-2">
            <Label htmlFor="description" className="text-white">Descrição</Label>
            <Input
              id="description"
              value={formData.description}
              onChange={(e) => setFormData({ ...formData, description: e.target.value })}
              className="bg-white/10 border-purple-500/20 text-white"
              required
            />
          </div>
          <div className="space-y-2">
            <Label htmlFor="notes" className="text-white">Observações</Label>
            <Input
              id="notes"
              value={formData.notes}
              onChange={(e) => setFormData({ ...formData, notes: e.target.value })}
              className="bg-white/10 border-purple-500/20 text-white"
            />
          </div>
          <div className="flex justify-end space-x-4">
            <Button
              type="button"
              variant="outline"
              onClick={() => onOpenChange(false)}
              className="border-purple-500/20 text-white hover:bg-purple-500/10"
            >
              Cancelar
            </Button>
            {createdTransactionId && formData.paymentMethod === 'mercado_pago' ? (
              <Button
                type="button"
                onClick={handleMercadoPagoPayment}
                disabled={paymentLoading}
                className="bg-blue-600 hover:bg-blue-700"
              >
                {paymentLoading ? "Processando..." : "Pagar com Mercado Pago"}
              </Button>
            ) : (
              <Button
                type="submit"
                disabled={loading}
                className={type === 'income' ? 'bg-green-600 hover:bg-green-700' : 'bg-red-600 hover:bg-red-700'}
              >
                {loading ? "Salvando..." : "Salvar"}
              </Button>
            )}
          </div>
        </form>
      </DialogContent>
    </Dialog>
  )
}
