import axios from 'axios';
import { ApiProduct, ApiResponse } from "@/types/api"

const api = axios.create({
  baseURL: process.env.NEXT_PUBLIC_API_URL || 'http://localhost:3001/api',
  headers: {
    'Content-Type': 'application/json',
  },
});

// Add a request interceptor to add the auth token
api.interceptors.request.use((config) => {
  const token = localStorage.getItem('token');
  if (token) {
    config.headers.Authorization = `Bearer ${token}`;
  }
  return config;
});

// Add a response interceptor to handle errors
api.interceptors.response.use(
  (response) => response,
  (error) => {
    if (error.response?.status === 401) {
      // Handle unauthorized access
      localStorage.removeItem('token');
      window.location.href = '/login';
    }
    return Promise.reject(error);
  }
);

// Types
interface Customer {
  id: string;
  name: string;
  email?: string;
  phone: string;
  cpf?: string;
  address?: string;
  city?: string;
  state?: string;
  postalCode?: string;
  notes?: string;
}

interface Service {
  id: string;
  customerId: string;
  technicianId?: string;
  deviceType: string;
  brand?: string;
  model?: string;
  serialNumber?: string;
  problem: string;
  diagnosis?: string;
  solution?: string;
  priority: 'low' | 'medium' | 'high' | 'urgent';
  status: 'pending' | 'in_progress' | 'completed' | 'cancelled';
  estimatedCompletionDate?: string;
  completedAt?: string;
  cost?: number;
  price: number;
}

interface Transaction {
  id: string;
  type: 'income' | 'expense';
  category: string;
  description: string;
  amount: number;
  date: string;
  customerId?: string;
  productId?: string;
  serviceId?: string;
  paymentMethod?: string;
  status: 'pending' | 'completed' | 'cancelled';
  notes?: string;
}

// Auth
export const login = (email: string, password: string) =>
  api.post<{ token: string; user: any }>('/auth/login', { email, password });

// Customers
export const getCustomers = () => 
  api.get<Customer[]>('/customers');

export const getCustomer = (id: string) =>
  api.get<Customer>(`/customers/${id}`);

export const createCustomer = (data: Omit<Customer, 'id'>) =>
  api.post<Customer>('/customers', data);

export const updateCustomer = (id: string, data: Partial<Customer>) =>
  api.put<Customer>(`/customers/${id}`, data);

export const deleteCustomer = (id: string) =>
  api.delete(`/customers/${id}`);

export const getCustomerStatistics = () =>
  api.get<{
    totalCustomers: number;
    newCustomersThisMonth: number;
    totalRevenue: number;
    averageRevenuePerCustomer: number;
  }>('/customers/statistics');

// Products
export const getProducts = () =>
  api.get<ApiResponse<ApiProduct[]>>('/products');

export const getProduct = (id: string) =>
  api.get<ApiResponse<ApiProduct>>(`/products/${id}`);

export const createProduct = (data: Omit<ApiProduct, 'id'>) =>
  api.post<ApiResponse<ApiProduct>>('/products', data);

export const updateProduct = (id: string, data: Partial<ApiProduct>) =>
  api.put<ApiResponse<ApiProduct>>(`/products/${id}`, data);

export const deleteProduct = (id: string) =>
  api.delete<ApiResponse<void>>(`/products/${id}`);

export const adjustStock = (id: string, quantity: number) =>
  api.post<ApiResponse<ApiProduct>>(`/products/${id}/adjust-stock`, { quantity });

export const getProductStatistics = () =>
  api.get<{
    totalProducts: number;
    lowStock: number;
    outOfStock: number;
    totalValue: number;
  }>('/products/statistics');

// Services
export const getServices = () =>
  api.get<Service[]>('/services');

export const getService = (id: string) =>
  api.get<Service>(`/services/${id}`);

export const createService = (data: Omit<Service, 'id'>) =>
  api.post<Service>('/services', data);

export const updateService = (id: string, data: Partial<Service>) =>
  api.put<Service>(`/services/${id}`, data);

export const deleteService = (id: string) =>
  api.delete(`/services/${id}`);

export const getServiceStatistics = () =>
  api.get<{
    totalServices: number;
    pendingServices: number;
    inProgressServices: number;
    completedServices: number;
    completedThisMonth: number;
    totalRevenue: number;
    averageServiceTime: number;
  }>('/services/statistics');

// Transactions
export const getTransactions = () =>
  api.get<Transaction[]>('/transactions');

export const getTransaction = (id: string) =>
  api.get<Transaction>(`/transactions/${id}`);

export const createTransaction = (data: Omit<Transaction, 'id'>) =>
  api.post<Transaction>('/transactions', data);

export const updateTransaction = (id: string, data: Partial<Transaction>) =>
  api.put<Transaction>(`/transactions/${id}`, data);

export const createMercadoPagoPayment = (transactionId: string) =>
  api.post(`/transactions/${transactionId}/payment`);

export const deleteTransaction = (id: string) =>
  api.delete(`/transactions/${id}`);

export const getTransactionStatistics = () =>
  api.get<{
    monthlyTotals: { type: string; total: number }[];
    categoryTotals: { type: string; category: string; total: number }[];
    dailyTotals: { date: string; type: string; total: number }[];
    profit: number;
  }>('/transactions/statistics');

export default api;
