"use client"

import { useState } from "react"
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { TransactionDialog } from "@/components/dialogs/transaction-dialog"
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table"
import { Badge } from "@/components/ui/badge"
import { FinancialChart } from "@/components/financial-chart"

type Transaction = {
  id: string
  type: 'income' | 'expense'
  category: string
  description: string
  amount: number
  date: string
  status: string
  paymentMethod: string
  customer?: string
}

const mockTransactions: Transaction[] = [
  {
    id: "1",
    type: "income",
    category: "Serviço",
    description: "Manutenção iPhone 12",
    amount: 899.90,
    date: "2023-11-15",
    status: "completed",
    paymentMethod: "PIX",
    customer: "João Silva"
  },
  {
    id: "2",
    type: "expense",
    category: "Peças",
    description: "Compra de telas",
    amount: 2500.00,
    date: "2023-11-14",
    status: "pending",
    paymentMethod: "Transferência"
  }
]

export default function FinancialPage() {
  const [search, setSearch] = useState("")
  const [transactions] = useState<Transaction[]>(mockTransactions)
  const [incomeDialogOpen, setIncomeDialogOpen] = useState(false)
  const [expenseDialogOpen, setExpenseDialogOpen] = useState(false)

  const getStatusInfo = (status: string) => {
    const statusMap = {
      completed: { label: "Concluído", color: "bg-green-500" },
      pending: { label: "Pendente", color: "bg-yellow-500" },
      cancelled: { label: "Cancelado", color: "bg-red-500" }
    }
    return statusMap[status as keyof typeof statusMap]
  }

  const getTypeInfo = (type: 'income' | 'expense') => {
    return type === 'income'
      ? { label: "Entrada", color: "text-green-500" }
      : { label: "Saída", color: "text-red-500" }
  }

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <h1 className="text-2xl font-semibold text-white">
          Controle Financeiro
        </h1>
        <div className="flex gap-4">
          <Button 
            className="bg-green-600 hover:bg-green-700"
            onClick={() => setIncomeDialogOpen(true)}
          >
            Nova Entrada
          </Button>
          <Button 
            className="bg-red-600 hover:bg-red-700"
            onClick={() => setExpenseDialogOpen(true)}
          >
            Nova Saída
          </Button>
        </div>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        <Card className="p-6 bg-black/20 backdrop-blur-sm border-purple-500/20">
          <div className="flex items-center gap-4">
            <div className="p-3 bg-green-500/20 rounded-lg">
              <svg className="w-6 h-6 text-green-500" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M7 11l5-5m0 0l5 5m-5-5v12" />
              </svg>
            </div>
            <div>
              <p className="text-sm text-gray-400">Entradas do Mês</p>
              <p className="text-2xl font-semibold text-white">R$ 15.000</p>
            </div>
          </div>
        </Card>

        <Card className="p-6 bg-black/20 backdrop-blur-sm border-purple-500/20">
          <div className="flex items-center gap-4">
            <div className="p-3 bg-red-500/20 rounded-lg">
              <svg className="w-6 h-6 text-red-500" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M17 13l-5 5m0 0l-5-5m5 5V6" />
              </svg>
            </div>
            <div>
              <p className="text-sm text-gray-400">Saídas do Mês</p>
              <p className="text-2xl font-semibold text-white">R$ 8.000</p>
            </div>
          </div>
        </Card>

        <Card className="p-6 bg-black/20 backdrop-blur-sm border-purple-500/20">
          <div className="flex items-center gap-4">
            <div className="p-3 bg-purple-600/20 rounded-lg">
              <svg className="w-6 h-6 text-purple-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 8c-1.657 0-3 .895-3 2s1.343 2 3 2 3 .895 3 2-1.343 2-3 2m0-8c1.11 0 2.08.402 2.599 1M12 8V7m0 1v8m0 0v1m0-1c-1.11 0-2.08-.402-2.599-1M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
              </svg>
            </div>
            <div>
              <p className="text-sm text-gray-400">Lucro do Mês</p>
              <p className="text-2xl font-semibold text-white">R$ 7.000</p>
            </div>
          </div>
        </Card>

        <Card className="p-6 bg-black/20 backdrop-blur-sm border-purple-500/20">
          <div className="flex items-center gap-4">
            <div className="p-3 bg-yellow-500/20 rounded-lg">
              <svg className="w-6 h-6 text-yellow-500" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 8v4l3 3m6-3a9 9 0 11-18 0 9 9 0 0118 0z" />
              </svg>
            </div>
            <div>
              <p className="text-sm text-gray-400">Pendentes</p>
              <p className="text-2xl font-semibold text-white">R$ 2.500</p>
            </div>
          </div>
        </Card>
      </div>

      <FinancialChart />

      <Card className="p-6 bg-black/20 backdrop-blur-sm border-purple-500/20">
        <div className="flex items-center gap-4 mb-6">
          <Input
            placeholder="Buscar transações..."
            value={search}
            onChange={(e) => setSearch(e.target.value)}
            className="bg-white/10 border-purple-500/20 text-white"
          />
        </div>

        <div className="rounded-lg border border-purple-500/20 overflow-hidden">
          <Table>
            <TableHeader className="bg-purple-500/10">
              <TableRow>
                <TableHead className="text-gray-300">Data</TableHead>
                <TableHead className="text-gray-300">Tipo</TableHead>
                <TableHead className="text-gray-300">Categoria</TableHead>
                <TableHead className="text-gray-300">Descrição</TableHead>
                <TableHead className="text-gray-300">Cliente</TableHead>
                <TableHead className="text-gray-300">Valor</TableHead>
                <TableHead className="text-gray-300">Método</TableHead>
                <TableHead className="text-gray-300">Status</TableHead>
                <TableHead className="text-gray-300">Ações</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {transactions.map((transaction) => {
                const status = getStatusInfo(transaction.status)
                const type = getTypeInfo(transaction.type)
                return (
                  <TableRow key={transaction.id} className="hover:bg-purple-500/5">
                    <TableCell className="text-gray-300">
                      {new Date(transaction.date).toLocaleDateString()}
                    </TableCell>
                    <TableCell>
                      <span className={type.color}>{type.label}</span>
                    </TableCell>
                    <TableCell className="text-gray-300">{transaction.category}</TableCell>
                    <TableCell className="text-gray-300">{transaction.description}</TableCell>
                    <TableCell className="text-gray-300">{transaction.customer || '-'}</TableCell>
                    <TableCell className={type.color}>
                      R$ {transaction.amount.toFixed(2)}
                    </TableCell>
                    <TableCell className="text-gray-300">{transaction.paymentMethod}</TableCell>
                    <TableCell>
                      <Badge className={`${status.color} text-white border-none`}>
                        {status.label}
                      </Badge>
                    </TableCell>
                    <TableCell>
                      <div className="flex items-center gap-2">
                        <Button variant="ghost" size="sm" className="text-purple-500 hover:text-purple-400 hover:bg-purple-500/10">
                          Editar
                        </Button>
                        {transaction.type === 'income' && (
                          <Button variant="ghost" size="sm" className="text-purple-500 hover:text-purple-400 hover:bg-purple-500/10">
                            Gerar NFe
                          </Button>
                        )}
                      </div>
                    </TableCell>
                  </TableRow>
                )
              })}
            </TableBody>
          </Table>
        </div>
      </Card>

      <TransactionDialog 
        open={incomeDialogOpen}
        onOpenChange={setIncomeDialogOpen}
        type="income"
      />

      <TransactionDialog 
        open={expenseDialogOpen}
        onOpenChange={setExpenseDialogOpen}
        type="expense"
      />
    </div>
  )
}
