"use client"

import { Card } from "@/components/ui/card"
import {
  Area,
  AreaChart,
  ResponsiveContainer,
  Tooltip,
  XAxis,
  YAxis,
} from "recharts"

const data = [
  { name: "Jan", value: 3500 },
  { name: "Fev", value: 4200 },
  { name: "Mar", value: 3800 },
  { name: "Abr", value: 4100 },
  { name: "Mai", value: 3200 },
  { name: "Jun", value: 4800 },
  { name: "Jul", value: 4300 },
  { name: "Ago", value: 3900 },
  { name: "Set", value: 4600 },
  { name: "Out", value: 4200 },
  { name: "Nov", value: 4700 },
  { name: "Dez", value: 4500 },
]

export function FinancialChart() {
  return (
    <Card className="p-6 bg-black/20 backdrop-blur-sm border-purple-500/20">
      <div className="flex items-center justify-between mb-6">
        <h2 className="text-lg font-semibold text-white">
          Movimentações Financeiras
        </h2>
      </div>
      <div className="h-[300px] w-full">
        <ResponsiveContainer width="100%" height="100%">
          <AreaChart
            data={data}
            margin={{ top: 0, right: 0, left: 0, bottom: 0 }}
          >
            <defs>
              <linearGradient id="gradient" x1="0" y1="0" x2="0" y2="1">
                <stop offset="0%" stopColor="#9333EA" stopOpacity={0.5} />
                <stop offset="100%" stopColor="#9333EA" stopOpacity={0} />
              </linearGradient>
            </defs>
            <XAxis
              dataKey="name"
              stroke="#6B7280"
              fontSize={12}
              tickLine={false}
              axisLine={false}
            />
            <YAxis
              stroke="#6B7280"
              fontSize={12}
              tickLine={false}
              axisLine={false}
              tickFormatter={(value) => `R$ ${value}`}
            />
            <Tooltip
              content={({ active, payload }) => {
                if (active && payload && payload.length) {
                  return (
                    <div className="rounded-lg border border-purple-500/20 bg-black/90 p-2 shadow-md backdrop-blur-sm">
                      <div className="grid grid-cols-2 gap-2">
                        <div className="flex flex-col">
                          <span className="text-[0.70rem] uppercase text-gray-400">
                            Valor
                          </span>
                          <span className="font-bold text-white">
                            R$ {payload[0].value}
                          </span>
                        </div>
                      </div>
                    </div>
                  )
                }
                return null
              }}
            />
            <Area
              type="monotone"
              dataKey="value"
              stroke="#9333EA"
              strokeWidth={2}
              fill="url(#gradient)"
              animationDuration={2000}
            />
          </AreaChart>
        </ResponsiveContainer>
      </div>
    </Card>
  )
}
